.list {
    width: 400px;
    margin-left: 24px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    list-style: none;
    padding: 0;
}

.item {
    display: flex;
    justify-content: space-between;
    align-items: stretch;

    padding-left: 5px;

    background-color: rgb(46, 158, 173);

    border-radius: 5px;
    border: 2px rgb(46, 158, 173);
}

.item__button {
    border-style: none;
    color: #fff;
    background-color:rgb(46, 158, 173);
    cursor: pointer;
}

.item__button:hover {
    background-color: rgb(46, 158, 173);
}